"use client";

import Link from "next/link";
import { useAccount, useConnect, useDisconnect } from "wagmi";
import { injected } from "wagmi/connectors";
import { Button } from "@/components/ui/button";
import { useState, useEffect } from "react";

export function Navbar() {
    const { address, isConnected } = useAccount();
    const { connect } = useConnect();
    const { disconnect } = useDisconnect();
    const [mounted, setMounted] = useState(false);

    useEffect(() => {
        setMounted(true);
    }, []);

    if (!mounted) return null;

    return (
        <nav className="flex items-center justify-between p-4 border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
            <Link href="/" className="text-xl font-bold">
                ChainTicket+
            </Link>
            <div className="flex items-center gap-4">
                <Link href="/create-event" className="text-sm font-medium hover:underline">
                    Create Event
                </Link>
                <Link href="/events" className="text-sm font-medium hover:underline">
                    Browse Events
                </Link>
                <Link href="/my-tickets" className="text-sm font-medium hover:underline">
                    My Tickets
                </Link>
                {isConnected ? (
                    <div className="flex items-center gap-2">
                        <span className="text-sm text-muted-foreground">
                            {address?.slice(0, 6)}...{address?.slice(-4)}
                        </span>
                        <Button variant="outline" size="sm" onClick={() => disconnect()}>
                            Disconnect
                        </Button>
                    </div>
                ) : (
                    <Button size="sm" onClick={() => connect({ connector: injected() })}>
                        Connect Wallet
                    </Button>
                )}
            </div>
        </nav>
    );
}
